.custom-button {
    min-width: 165px;
    width: auto;
    height: 50px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-family: 'Open Sans Condensed';
    font-weight: bolder;
    border: none;
    cursor: pointer;
  }

  .custom-button:hover {
    background-color: white;
    color: black;
    border: 1px solid black;
  }

  .google-sign-in{
      background-color: #4285f4;
      color: white;
  }
  .google-sign-in:hover{
    background-color: #357ae8;
    border: none;
  }
  .inverted{
    background-color: white;
    color: black;
    border: 1px solid black;
    width: 80%;
  }
  .inverted:hover{
    background-color: black;
    color: white;
    border:none;

  }